<template>
  <div>
    <mavon-editor
      :placeholder="config.placeholder"
      :toolbarsFlag="config.toolbarsFlag"
      :toolbars="toolbars"
      style="height:500px"
      v-model="markdown.value"
      @change="change"
      ref=md
      @save="saveBefore"
    ></mavon-editor>
    <!---->
    <el-dialog title="" :visible.sync="dialogVisible">
      <el-form :model="markdown" :rules="rules" ref="markdown">
        <el-form-item label="" prop="title" :label-width="labelWidth">
          <el-input v-model="markdown.questionTitle" maxlength="30" show-word-limit type="text" auto-complete="false" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
  name: 'markdown',
  components: {
    mavonEditor
  },
  data () {
    return {
      dialogVisible: false,
      labelWidth: '70px',
      config: {
        placeholder: ' ',
        toolbarsFlag: true
      },
      markdown: {
        value: '',
        questionContent: '',
        questionTitle: ''
      },
      rules: {
        questionTitle: [{ required: true, message: '', trigger: 'blur' }]
      },
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: false, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: false, // 
        subscript: false, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        imagelink: false, // 
        upload: false,
        code: true, // code
        table: true, // 
        fullscreen: false, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: false, // 
        /* 2.1.8 */
        alignleft: false, // 
        aligncenter: false, // 
        alignright: false, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      }
    }
  },
  methods: {
    // markdown
    change (value, render) {
      this.markdown.questionContent = render
    },
    saveBefore () {
      if (this.markdown.value) {
        this.dialogVisible = true
      } else {
        this.$message.error('')
      }
    },
    save () {
      this.$refs.markdown.validate((valid) => {
        if (valid) {
          this.postRequest2('/question', this.markdown)
          this.dialogVisible = false
        } else {
          this.$message.error('')
          return false
        }
      })
    }
  }
}
</script>

